import React, { Component } from 'react'

class Cell extends Component {
  componentDidMount() {
    // Set stizes to the local state
    this.setState({
      width: this.elem.clientWidth,
      offsetX: this.elem.getBoundingClientRect().left
    })
  }

  render() {
    var classes = ""
      
    if (this.props.class)
      classes = "_"+this.props.class

    if (this.props.header)
      classes += " Header"

    if (this.props.readonly)
      classes += " ro"

    if (this.props.annule)
      classes += " line"

    return <td ref={(elem) => this.elem=elem} className={classes} onClick={(e) => {this.props.tap(e, this)}}>{this.props.value}</td>
  }
}

export default Cell 
