import React, { Component } from 'react'
import Board from "./Components/Board"
import Modal from './Components/Modal'
import './style.css'

function withIndex(arr) {
  return arr.map((v,i) => ({val: v, index: i}))
}

function trim(s) {
  return String(s).replace(/^\s+|\s+$/g, '')
}

class App extends Component {
  constructor() {
    super()
    this.state = {
      rows: [
        "name", 1, 2, 3, 4, 5, 6, "total", "bonus", "-1",
        "mini", "MAXI", "diff", "-2",
        "chance", "-3",
        "brelan", "suite", "full", "carre", "yams", "-4",
        "TOTAL"
      ],

      rowname: {
        "name": "",
        "diff": "différence",
        "chance": "Chance",
        "brelan": "Brelan",
        "suite": "Suite",
        "full": "Full",
        "carre": "Carré",
        "yams": "Yam's",
      },

      readonly: ["total", "bonus", "diff", "TOTAL"],

      players: [
      ],

      show_modal_new_player: false,
      show_modal_rename_player: false,
    }
  }

  add_player = (event, immed) => {
    var name
    event.preventDefault()
    name = immed === undefined ? trim(this.textInput.value) : immed
    name = name.toLowerCase()
    if (name === "") {
      alert("Entrez un nom de joueur !")
    } else
    if (this.state.players.filter(obj=>obj.name === name).length === 0) {
      console.log("Joueur ajouté: ", name)
      this.state.players.push({name: name})
      this.toggle_modal_new_player()
    } else {
      alert("Le joueur " + name + " existe déja !")
    }
  }

  rename_player = (event, immed) => {
    event.preventDefault()
    var name = this.state.show_modal_rename_player_name
    var new_name = immed === undefined ? this.state.show_modal_rename_player_new_name : immed
    new_name = new_name.toLowerCase()
    console.log('rename:', name, '-->', new_name)

    if (this.state.players.filter(obj=>obj.name === new_name).length !== 0) {
      console.log(new_name, name)
      if (new_name === name)
        alert("Choisissez un nom différent !")
      else
        alert("Le joueur " + new_name + " existe déja !")
      return
    }

    var indexes = withIndex(this.state.players).filter(obj => obj.val.name === name)
    if (indexes.length === 0) {
      alert("Erreur : player " + name + " not found in App state")
      return
    }
    var index = indexes[0].index // Index of player in the this.state.players array
    var new_state = this.state
    new_state.players[index].name = new_name
    new_state.show_modal_rename_player = !new_state.show_modal_rename_player
    this.setState(new_state)
  }

  delete_player = (event, immed) => {
    event.preventDefault()
    var name = this.state.show_modal_rename_player_name

    if (window.confirm('Vraiment supprimer le joueur "' + name + '" ?')) {
      console.log("del player", name)
      var indexes = withIndex(this.state.players).filter(obj => obj.val.name === name)
      if (indexes.length === 0) {
        alert("Erreur : player " + name + " not found in App state")
        return
      }
      var index = indexes[0].index // Index of player in the this.state.players array
      var new_state = this.state
      new_state.players.splice(index, 1)
      new_state.show_modal_rename_player = !new_state.show_modal_rename_player
      this.setState(new_state)
    }
    else
      console.log("deletion of player", name, "cancelled")
  }

  toggle_modal_new_player = () => {
    this.setState({
      show_modal_new_player: !this.state.show_modal_new_player
    })
  }

  toggle_modal_rename_player = () => {
    this.setState({
      show_modal_rename_player: !this.state.show_modal_rename_player
    })
  }

  rename_change = (event) => {
    this.setState({show_modal_rename_player_new_name: this.textInput.value})
  }

  new_game = (event) => {
    event.preventDefault()
    if (window.confirm("Remettre à zéro ?")) {
      console.log("new game")
      var names = []
      this.state.players.forEach((val, index, array) => {
        names.push({name: val.name})
      })
      this.setState({players: names})
    }
    else
      console.log("new game cancelled")
  }

  cell_tapped = (event, cell) => {
    var what = cell.props.class  // cell class (its name)
    var name = cell.props.player // player name
    if (what === 'name') {
      this.setState({
        show_modal_rename_player_new_name: name,
        show_modal_rename_player_name: name,
        show_modal_rename_player: true
      })
    } else {
      var left = (event.clientX-cell.state.offsetX) < (cell.state.width/2)
      this.process_tap(name, what, left)
    }
  }

  render() {
    var modal_new_player = null
    if (this.state.show_modal_new_player)
      modal_new_player = (
        <Modal width="360px" height="290px"
               focus={() => {this.textInput.focus() }} close={this.toggle_modal_new_player}>
          <form action='#'>
            <label>Nom du joueur:</label><br/>
            <input type="text" id="new_player_name" ref={(input) => this.textInput = input} />
            <br />
            <button onClick={this.add_player} id="new_player_submit" >
              Ajouter
            </button><br/>
            <br/>
            Choix rapide:<br/>
            <button className="immed" onClick={(e) => {this.add_player(e, "Claude")}} >Claude</button>
            <button className="immed" onClick={(e) => {this.add_player(e, "Martine")}} >Martine</button>
            <button className="immed" onClick={(e) => {this.add_player(e, "Eric")}} >Eric</button>
            <button className="immed" onClick={(e) => {this.add_player(e, "Erlina")}} >Erlina</button>

          </form>
        </Modal>
      )

    var modal_rename_player = null
    if (this.state.show_modal_rename_player)
      modal_rename_player = (
        <Modal width="360px" height="340px"
               focus={() => {this.textInput.focus() }} close={this.toggle_modal_rename_player}>
          <form action='#'>
            <label>Nom du joueur:</label><br/>
            <input type="text" id="rename_player_name" ref={(input) => this.textInput = input}
                   value={this.state.show_modal_rename_player_new_name} onChange={this.rename_change} />
            <br />
            <button onClick={this.rename_player} id="rename_player_submit" >
              Renommer
            </button><br/>
            <button onClick={this.delete_player} id="delete_player_submit" >
              Supprimer 
            </button><br/>
            <br/>
            Choix rapide:<br/>
            <button className="immed" onClick={(e) => {this.rename_player(e, "Claude")}} >Claude</button>
            <button className="immed" onClick={(e) => {this.rename_player(e, "Martine")}} >Martine</button>
            <button className="immed" onClick={(e) => {this.rename_player(e, "Eric")}} >Eric</button>
            <button className="immed" onClick={(e) => {this.rename_player(e, "Erlina")}} >Erlina</button>
          </form>
        </Modal>
      )

    return (
      <div id="App">
        <Board
          players={this.state.players}
          rows={this.state.rows}
          readonly={this.state.readonly}
          rowname={this.state.rowname}
          show_new_player={this.toggle_modal_new_player}
          new_game={this.new_game}
          tap={this.cell_tapped}
        />
        {modal_new_player}
        {modal_rename_player}
      </div>
    )
  }

  process_tap = function(name, what, left) {
    console.log(name, 'tapped on', '"'+what +'"', left ? 'left' : 'right')
    // find the player in state
    var indexes = withIndex(this.state.players).filter(obj => obj.val.name === name)
    if (indexes.length === 0) {
      alert("Erreur : player " + name + " not found in App state")
      return
    }
    var index = indexes[0].index // Index of player in the this.state.players array
    var old_val, new_val, new_state, to_delete = false

    switch (what) {
      case 1: case 2: case 3: case 4: case 5: case 6:
        old_val = this.state.players[index][""+what]
        if (old_val === undefined)
          new_val = left ? -1 : 0
        else
          new_val = left ? old_val - 1 : old_val + 1

        if ((new_val === -4) || (new_val === 3))
          to_delete = true
        break

      case "brelan": case "suite": case "full": case "carre": case "yams":
        old_val = this.state.players[index][what]
        if (old_val === undefined)
          new_val = {
            'brelan': 10,
            'suite': 20,
            'full': 30,
            'carre': 40,
            'yams': 50
          }[what]
        else if (old_val === 'annule')
          to_delete = true
        else
          new_val = 'annule'
        break

      case "mini": case "MAXI": case "chance":
        old_val = this.state.players[index][what]
        if (old_val === undefined)
          new_val = {
            'mini': left ? 10 : 11,
            'MAXI': left ? 24 : 25,
            'chance': left ? 17 : 18,
          }[what]
        else
          new_val = left ? old_val - 1 : old_val + 1

        if ((new_val === 4) || (new_val === 31))
          to_delete = true
        break

      default:
        // We don't handle tapping on this cell
        return
    }

/*
    if (to_delete)
      console.log('deleting', what)
    else
      console.log('setting', what,'from', old_val, 'to', new_val)
*/

    new_state = this.state
    if (to_delete)
      delete new_state.players[index][what]
    else
      new_state.players[index][what] = new_val
    this.setState(new_state)
  }

  componentDidMount() {
    document.title = "Yam's!"
  }
}

export default App

