import React, { Component } from 'react'
import Cell from "./Cell"
import ToolBar from "./ToolBar"

function inArray(myArray, myValue) {
  var inArray = false
  myArray.map(key => {
    if (key === myValue)
      inArray = true
    return null
  })
  return inArray
}

class Board extends Component {
  render() {
    var rows = this.props.rows

    this.props.players.map((player, index) => {
      player["figures"] = 0
      player["bonus"] = 0
      player["total"] = 0
      return null
    })

    if (this.props.players && this.props.players.length) {

      rows = rows.map((key, index) => {

        if (String(key).startsWith("-"))
          return <tr className="sep" key={key}></tr>

        var skey = key
        if (this.props.rowname[key] !== undefined)
          skey = this.props.rowname[key]

        return <tr key={key} id={"row_"+key}>
          <Cell header key={"header-"+key} readonly={inArray(this.props.readonly, key)} value={skey}
                       tap={this.props.tap}/>
            {
              this.props.players.map((player, index) => {
                var annule = false
                var content

                /* Manual fields (inputs) */
                if (key in player) {
                  content = player[key]

                  switch (key) {
                    case 1: case 2: case 3: case 4: case 5: case 6:
                      player["total"] += (content+3)*key
                      content *= key

                      /* Align numbers by adding &nbsp; before and after*/
                      if (content >= 0) {
                        content = '\u00a0' + content
                        if (content < 10)
                          content += '\u00a0'
                      } else if (content > -10)
                          content += '\u00a0'
                      break

                    case "chance": case "brelan": case "suite": case "full": case "carre": case "yams":
                      if ((content !== "annule") && (content !== undefined))
                        player["figures"] += content
                      break

                    default:
                      break
                  }
                } else {
                  content = ""
                }

                var digits_seen = (1 in player) + (2 in player) + (3 in player) +
                                  (4 in player) + (5 in player) + (6 in player)

                /* Automatic Fields */
                switch (key) {
                  case "total":
                    if (digits_seen > 0)
                      content = player[key] = player["total"]
                    else
                      content = ""
                    break

                  case "bonus":
                    if (digits_seen === 6)
                      content = player[key] = (player["total"] >= 63) ? 30 : 0
                    else
                      content = ""
                    break

                  case "diff":
                    if ((1 in player) && ("mini" in player) && ("MAXI"in player))
                      content = player[key] = (player["MAXI"]-player["mini"])*(player[1]+3)
                    else
                      content = ''
                    break

                  case "TOTAL":
                    content = player["figures"] + player["total"]
                    if (digits_seen === 6)
                      content += player["bonus"]
                    if ((1 in player) && ("mini" in player) && ("MAXI"in player))
                      content += player["diff"]
                    if (content === 0)
                      content = ""
                    break

                  default:
                    break
                }

                if (content === "annule") {
                  annule = true
                  content = ""
                }

                return <Cell key={player.name+'-'+key} class={key} value={content} annule={annule}
                             tap={this.props.tap} player={player.name} />
              }
              )}
              <td className="Gluton" />
          </tr>
      })
    } else
      rows = []

    return (
      <table key="board" className="Board">
        <tbody key="board-body">
          {rows}
          <ToolBar key="toolbar" cols={rows.length}
                   show_new_player={this.props.show_new_player}
                   new_game={this.props.new_game}
          />
        </tbody>
      </table>
    )
  }
}

export default Board
